.auth-wrapper {
  display: block;
  justify-content: center;
  flex-direction: row;
  text-align: left;
}

.auth-inner {
  width: 70%;
  /* height: 70%; */
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 5px;
  transition: all 0.3s;
}
